@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .second {
        &_block {
            align-items: flex-start; } }
    .choice {
        padding: 55px 0;
        min-height: 840px; } }

@media (max-width: 991px) {
    nav {
        position: static; }
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_phone {
            font-size: 18px; }
        &_btn {
            width: 100%;
            margin-top: 21px;
            height: 40px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .second {
        &_block {
            align-items: center;
            flex-direction: column;
            margin-top: 20px;
            .row_pay {
                margin-right: 0; } }
        &_text {
            width: 300px;
            margin-top: 20px; } }
    .choice {
        min-height: 800px;
        &_img {
            width: 100%; } }
    .footer {
        &_wrapper {
            flex-wrap: wrap; }
        &_wrapper > div {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-bottom: 20px;
            &:nth-child(1) {
                width: 100%; }
            &:nth-child(3) {
                width: 100%; } } }
				justify-content: center; }







@media  (max-width: 767px) {
    .title {
        margin-top: 25px; }
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: 0.6s all;
        z-index: 1;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin-top: 10px; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 25px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: white;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                     transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            height: 40px;
            width: 200px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        &_header {
            line-height: 45px; } }
    .second {
        padding: 40px; }
    .mobile {
        padding: 40px; }
    .choice {
        min-height: 600px;
        padding: 40px 0; }

    .world {
        min-height: 450px; } }





@media  (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px; }
        &_phone {
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 11px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 6px;
            &:hover {
                background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            padding: 0 24px;
            font-size: 30px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            &:hover {
                background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 4px 0; }
        &_link {
            display: block; } }
    .second {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        .row_pay {
            width: 100px;
            height: 100px; }
        &_text {
            width: 100%;
            margin-top: 27px;
            .pay_title {
                font-size: 16px; } } }
    .mobile {
        padding: 20px 0 40px 0;
        .row {
            margin-top: 30px; }
        &_item {
            height: 254px;
            padding: 81px 40px 0 40px;
            &_subtitle {
                font-size: 18px;
                line-height: 31px; }
            &_plus {
                bottom: 28px;
                &:hover {
                    &:before, &::after {
                        background-color: $main-color; } } } } }


    .choice {
        padding: 25px 0 33px 0;
        min-height: 390px;
        background: #000;

        &_img {
            margin-top: 35px; }
        &_descr {
            margin-top: 23px;
            color: #ffffff;
            font-size: 17px;
            font-weight: 300px;
            line-height: 24px; }
        .black {
            display: none; } } }

