
header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 5; }

.hamburge {
	display: none; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		&:hover {
			text-decoration: none;
			color: $main-color; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	&_call {
		margin-top: 19px;
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		line-height: 14px;
		text-align: right; }

	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		line-height: 14px;
		text-align: right;
		line-height: 12px; }

	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }



.promo {
	min-height: 800px;
	background: url('../img/bg/Layer\ 3.png') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	&_header {
		color: #ffffff;
		font-weight: 700;
		font-size: 60px;
		text-transform: uppercase;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	&_subheader {
		color: #ffffff;
		font-weight: 700;
		font-size: 36px;
		text-transform: uppercase;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-align: center;
		@include btn_hover; } }

.second {
	position: relative;
	min-height: 865px;
	padding: 0 0 65px 0;

	&_divider {
		display: block;
		margin: 71px auto 0 auto;
		padding: 4px 11px 8px 10px;
		width: 112px;
		height: 24px;
		background-color: #000000; } }

p {
	color: #ffffff;
	font-family: "Roboto - Thin";
	font-size: 11px;
	text-align: center;
	text-transform: uppercase; }

.second {
	&_header {
		margin-top: 38px;
		margin-bottom: 40px;
		color: #222222;
		font-family: Roboto;
		font-size: 38px;
		font-weight: 700;
		font-style: normal;
		letter-spacing: normal;
		line-height: 48px;
		text-align: center;
		text-transform: uppercase; }

	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;

		.row_pay {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 113px;
			height: 113px;
			margin-right: 26px; } }

	&_text {
		width: 341px;

		.pay_title {
			color: #1eacc7;
			font-size: 17px;
			font-weight: 400; }

		.pay_descr {
			font-weight: 300;
			color: #202020;
			font-size: 15px;
			line-height: 21px; } } }

.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000;
		&_city {
			width: 76px; } } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	line-height: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff;
		&_we {
			margin: 20px 0 486px 0; } } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/bg3/bg3.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/icons/3/oformlenie.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/icons/3/nadez_podacha.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/icons/3/prozrach_ceny.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/icons/3/beznal_opl.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/icons/3/obratn_sv.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/icons/3/razdel_stoimost.png') center (center / cover) no-repeat; }
		&_subtitel {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main_color;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				height: 14px;
				width: 2px;
				background-color: $main_color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $second-color; } } } } }

.label_2 {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #fff;
	color: black;
	text-align: center;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase; }

.title_2 {
	margin-top: 32px;
	color: #fff;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }

.subtitle_2 {
	color: #fff;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/bg4/bg4.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }

	&_title {
		color: #1ca8c3;
		font-size: 22px;
		font-weight: 400; }

	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				width: 21px;
				height: 20px;
				background: url('../img/icons/5/_blue_png.png');
				left: -32px;
				top: 3px; } } }
	&_descr {
		color: #1ca8c3;
		font-weight: 300; }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&:before {
					background: url('../img/icons/5/red_png.png'); } } } } }

.world {
	position: relative;
	min-height: 717px;
	background: url('../img/bg6/bg_6.png') center (center / cover) no-repeat;
	padding: 49px 0;
	&_view {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }

.footer {
	&_line {
		margin: 0 auto;
		width: 299px;
		height: 4px;
		background-color: #1fbad6; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }

	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: white; } } }
	&_links {
		margin: 0 auto;
		width: 390px;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }

.xs-visible {
	display: none; }

